<template>
    <main>
      <div class="search-box search-fine-box">
        <ul class="search-input-box">
          <li class="datePicker">
            <span class="label">查询月份：</span>
            <el-date-picker
              v-model="month"
              :editable="false"
              ref="timePicker"
              type="month">
            </el-date-picker>
          </li>
          <li>
            <span class="label">处罚对象：</span>
            <sc-users
              type="2"
              v-model="queryContent.userId"
              :param="{types: '0,1,2,3,4,5'}"
              ref="userSelect"
              class="short-input"
              style="display: inline-block;"
            />
          </li>
        </ul>
        <div class="btn-box">
          <el-button type="primary" icon="el-icon-search" @click="onSearch">查询</el-button>
          <el-button type="success" icon="el-icon-download" @click="exportNew">导出</el-button>
        </div>
      </div>
      <div class="content-box">
        <div class="table-box" :style="{height: contentHeight + 'px'}" @click="onClickTable">
          <Spread :ngData="tableData" :headData="tableHead"></Spread>
        </div>
      </div>
      <!-- 导出 -->
      <csv
        :visible="exportObj.visible"
        :fileName="exportObj.fileName"
        :head="tableHead"
        :paramData="exportObj.excelData"
        :sum="exportObj.sum"
        :current="exportObj.current"
        @clear="closeDialog"
      />
    </main>
</template>

<script>
  import { pagerMixin } from "@/utils/mixin";
  import moment from 'moment'
  import { cloneDeep } from 'lodash'
  import { fineSummaryUrl, fineSummaryQuery } from '../../../../config/api'
  export default {
    name: "insideFineCollect",
    mixins: [pagerMixin],
    activated(){
      this.z_method.refreshSpread();
    },
    mounted() {
      this.$nextTick(() => {
        this.z_method.refreshSpread();
      });
    },
    data(){
      return {
        month: new Date(moment().format('YYYY-MM-DD') + ' 00:00:00'),
        queryContent: {},
        optionsClone: {},
        exportObj: {
          fileName: '罚款月汇总数据'
        },
        requestUrl: fineSummaryUrl,
        fineTypeOptions: [],
        tableData: [],
        tableHead: {
          dblclick: (v, row, args) => {
            if (args.col === 3) {
              let copyRow = JSON.parse(JSON.stringify(row));
              copyRow.col = args.col;
              this.$emit("goto", "detail", copyRow);
            }
          },
          columns: [
            { displayName: '业务月份', name: 'businessDate', size: 150, formatter: 'yyyy-MM', isCode: true},
            { displayName: '内部处罚人', name: 'interMan', size: 150},
            { displayName: '总处罚条数', name: 'fineCount', size: 150},
            {
              displayName: '总部罚款金额',
              name: 'headFineFee',
              size: 150,
              showTotal: true
            },
            { displayName: '内部罚款金额', name: 'interFineFee', size: 150},
            { displayName: '已登记处罚金额', name: 'registerFineFee', size: 150},
            { displayName: '已入预付款金额', name: 'advanceFineFee', size: 150}
          ]
        },
      }
    },
    methods: {
      onSearch() {
        if(!this.month){
          this.$message({
            message: '时间不能为空',
            duration: 2000,
            type: 'warning'
          });
          return
        }
        this.queryContent.month = moment(this.month).format('YYYY-MM-DD');
        this.query(this.queryContent)
      },
      query(param){
        if(param){
          this.optionsClone = cloneDeep(param);
        }
        fineSummaryQuery(this.optionsClone).then(res => {
          if( res.code === 0 && res.data && res.data.length > 0){
            this.tableData = res.data;
          }else if(res.code === 0){
            this.msg('暂无数据！');
            this.tableData = [];
          }
        }).catch(err => {
          this.tableData = [];
        })
      },
      exportNew(){
        if(this.tableData.length > 0){
          this.checklistData = this.tableData;
          this.checklistExport()
        }else{
          this.msg('没有数据无法导出!')
        }
      },
      onClickTable(){
        this.$refs.timePicker._data.pickerVisible = false;
        this.$refs.userSelect.autoBlur();
      }
    },
    computed: {
      contentHeight() {
        if(this.z_param.isActiveH){
          return this.$store.getters.clientHeight - 170;
        }else{
          if (this.z_param.isNode) {
            return this.$store.getters.clientHeight - 220;
          } else {
            return this.$store.getters.clientHeight - 160;
          }
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .search-box{
    display: flex;
    justify-content: space-between;
    padding: 15px 20px;
    border-bottom: 10px solid #e9eef2;
    .search-input-box{
      display: flex;
      li{
        margin-right: 10px;
        .label{
          color: #595959;
        }
      }
      .short-input{
        width: 170px;
      }
    }
  }
  .content-box{
    padding-bottom: 20px;
  }
</style>

<style>
  .search-fine-box .datePicker .el-date-editor.el-input, .el-date-editor.el-input__inner {
    width: 135px;
  }
</style>
