<template>
  <main>
    <div class="search-fine-box">
      <bill-code v-model="billCode"></bill-code>
      <ul class="search-input-box">
        <li>
          <span class="label">结算扣款时间：</span>
          <el-date-picker
            v-model="queryTime.startDate"
            type="datetime"
            :editable="false"
            @change="startDateChange"
            ref="timePickerA">
          </el-date-picker>
          至
          <el-date-picker
            v-model="queryTime.endDate"
            :editable="false"
            default-time="23:59:59"
            ref="timePickerB"
            type="datetime">
          </el-date-picker>
        </li>
        <li>
          <span class="label">结算流水号：</span>
          <el-input v-model="queryContent.fineWater" class="short-input" :maxlength="40"></el-input>
        </li>
        <li>
          <span class="label">结算编码：</span>
          <el-input v-model="queryContent.fineNumber" class="short-input" :maxlength="40"></el-input>
        </li>
        <li>
          <span class="label">内部判罚状态：</span>
          <el-select v-model="queryContent.interStatus" placeholder=" " ref="statusSelect" class="select-input">
            <el-option
              v-for="item in fineStatusOptions"
              :key="item.id"
              :label="item.label"
              :value="item.id">
            </el-option>
          </el-select>
        </li>
        <li>
          <span class="label">罚款类型：</span>
          <el-select v-model="queryContent.fineTypeCode" placeholder=" " ref="typeSelect" class="select-input">
            <el-option
              v-for="item in fineTypeOptions"
              :key="item.feeTypeCode"
              :label="item.feeType"
              :value="item.feeTypeCode">
            </el-option>
          </el-select>
        </li>
        <li>
          <span class="label">处罚对象：</span>
          <sc-users
            type="2"
            v-model="queryContent.interUserId"
            :param="{types: '0,1,2,3,4,5'}"
            ref="userSelect"
            class="short-input"
            style="display: inline-block;"
          />
        </li>
      </ul>
    </div>
    <div class="content-box">
      <div class="btn-box">
        <!-- <el-button type="primary" @click="batchUpdate">同步账单揽收业务员</el-button>
        <el-button type="primary" @click="batchUpdate">同步最新派件业务员</el-button> -->
        <el-button type="primary" @click="batchUpdate">同步最新账单结算对象</el-button>
        <el-button type="primary" @click="chooseUser">编辑业务员</el-button>
        <el-tooltip class="item" effect="dark" content="可选择单条或多条数据进行判罚操作" placement="top-start">
          <el-button type="danger" @click="interFine">内部判罚</el-button>
        </el-tooltip>
        <el-button type="danger" @click="batchUpdate">取消判罚</el-button>
        <el-button type="primary" icon="el-icon-search" @click="onSearch">查询</el-button>
        <el-button type="success" icon="el-icon-download" @click="exportNew">导出</el-button>
      </div>
      <div :style="{height: contentHeight + 'px'}" @click="onClickTable">
        <Spread :ngData="tableData" :headData="tableHead" @checkboxAllClick="checkboxAllClick"></Spread>
      </div>
      <div class="page-box">
        <sz-pagination
          :page="pagination.currentPage"
          :pageSize="pagination.pageSize"
          :total="pagination.total"
          @fetchData="fetchData"/>
      </div>
    </div>
    <!-- 导出 -->
    <csv
      :visible="exportObj.visible"
      :fileName="exportObj.fileName"
      :head="tableHead"
      :paramData="exportObj.excelData"
      :sum="exportObj.sum"
      :current="exportObj.current"
      @clear="closeDialog"
    />
    <!-- 批量 -->
    <sz-batch-process
      :title="title"
      :height="500"
      :dialogVisible="dialogVisible"
      :columns="columns"
      :tableData="currentRow"
      :batchUrl="batchUrl"
      @model="close"
    />
    <!--编辑业务员-->
    <el-dialog
      title="编辑业务员"
      :close-on-click-modal="false"
      :visible.sync="editDialogVisible"
      :modal-append-to-body="false"
      width="400px">
      <div class="user-form">
        <span>关联业务员：</span>
        <sc-users
          type="2"
          popper-class="max-index"
          v-model="userId"
          :param="{types: '0,1,2,3,4,5'}"
          style="display: inline-block;width: 180px;"
        />
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editBatchUpdate">确 定</el-button>
      </span>
    </el-dialog>
    <!--内部判罚-->
    <el-dialog
      v-drag-dialog
      title="内部判罚"
      :visible.sync="interFineDialogVisible"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      width="400px">
      <div class="user-form">
        <span class="form-title">单号：</span>
        <el-input v-model="interFineParam.billCode" class="form-input" :disabled="true"></el-input>
      </div>
      <div class="user-form">
        <span class="form-title">关联业务员：</span>
        <el-input v-model="interFineParam.scanMan" class="form-input" :disabled="true"></el-input>
      </div>
      <div class="user-form">
        <span class="form-title">内部处罚人：</span>
        <sc-users type="2" v-model="interFineParam.interUserId" :param="{types: '0,1,2,3,4,5'}" popper-class="max-index" class="form-input"/>
        <!-- <sc-users type="7" :param="{types: '0,2,4'}" v-model="interFineParam.interUserId" popper-class="max-index" class="form-input" /> -->
        <!-- <el-input v-model="interFineParam.interUserName" class="form-input" :disabled="true"></el-input> -->
      </div>
      <div class="user-form">
        <span class="form-title">总部罚款金额：</span>
        <el-input v-model="interFineParam.headFineFee" class="form-input" :disabled="true"></el-input>
      </div>
      <div class="user-form">
        <span class="form-title">罚款类型：</span>
        <el-select v-model="interFineParam.interFineCode" placeholder="请选择类型" ref="typeSelect" class="form-input" filterable>
          <el-option
            v-for="item in interFineOptions"
            :key="item.id"
            :label="item.feeName"
            :value="item.id">
          </el-option>
        </el-select>
        <!--<el-input v-model="interFineParam.input" class="form-input"></el-input>-->
      </div>
      <div class="user-form">
        <span class="form-title">内部处罚金额：</span>
        <el-input v-model="interFineParam.interFineFee" class="form-input" maxlength="10"></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="registerInterFine(1)">只登记</el-button>
        <el-button type="primary" @click="registerInterFine(2)">登记并扣预付款</el-button>
      </span>
    </el-dialog>
  </main>
</template>

<script>
  import { pagerMixin } from "@/utils/mixin";
  import {fineTypeAjax, fineDetailUrl, fineDetailQuery, updateCollectmanAjax, updatePieAjax, updateFineUserAjax, judgeFineAjax, interFineAjax, interFineTypeAjax} from '@/config/api'
  import moment from 'moment'
  import { trim,cloneDeep } from 'lodash'
  export default {
    name: "fineDetail",
    mixins: [pagerMixin],
    props: {
      switchOptions: {
        type: Object,
        default: {}
      }
    },
    created(){
      fineTypeAjax().then(res => {
        this.fineTypeOptions = cloneDeep(res.data);
        this.fineTypeOptions.unshift({
          feeType: '全部',
          feeTypeCode: -1
        });
        this.queryContent.fineTypeCode = -1;
      });
      let opts = {
        parentId: 22,
        sign: 0
      };
      interFineTypeAjax(opts).then(res => {
        this.interFineOptions = cloneDeep(res.data);
      })
    },
    activated(){
      this.z_method.refreshSpread();
    },
    mounted() {
      this.$nextTick(() => {
        this.z_method.refreshSpread();
      });
    },
    data(){
      return {
        queryContent: {
          billCode: '',
          settlementStartDate: '',
          settlementEndDate: '',
          fineWater: '',      //结算流水号
          fineNumber: '',     //结算编码
          interStatus: -1,    //内部判罚状态
          fineTypeCode: '',   //罚款类型
          interUserId: ''     //处罚对象
        },
        queryTime: {
          startDate: new Date(moment().format('YYYY-MM-DD') + ' 00:00:00'),
          endDate: new Date(moment().format('YYYY-MM-DD') + ' 23:59:59')
        },
        billCode: '',
        tableData: [],
        tableHead: {
          rowStyle: function (row) {
            if (row.interStatusName != '未处罚') return {foreColor: "green"}
          },
          columns: [
            {
              displayName: '选择',
              name: 'check',
              cellType: new GC.Spread.Sheets.CellTypes.CheckBox(),
              isHideFilter: false,
              size: 50,
              isEdit: true,
              allCheck: true,
              onchange: (row, val) => {
                this.selectionSite(row, val);
              },
              dblclick: row => {
                this.selectionSite();
              }
            },
            { displayName: '结算扣款时间', name: 'fineDate', size: 130, formatter: 'yyyy-MM-dd hh:mm:ss'},
            { displayName: '结算流水号', name: 'fineWater', isCode: true},
            { displayName: '结算编码', name: 'fineNumber', isCode: true},
            { displayName: '单号',
              name: 'billCode',
              isCode: true ,
              dblclick: val => {
                window.open(this.z_param.billsUrl + val);
              }
            },
            { displayName: '罚款类型', name: 'fineType'},
            { displayName: '总部罚款金额', name: 'headFineFee',showTotal: true},
            { displayName: '业务发生时间', name: 'scanDate', size: 130, formatter: 'yyyy-MM-dd hh:mm:ss'},
            { displayName: '关联业务员', name: 'scanMan'},
            { displayName: '内部处罚人', name: 'interUserName'},
            { displayName: '内部处罚金额', name: 'interFineFee'},
            { displayName: '内部判罚状态', name: 'interStatusName'},
            { displayName: '结算中心', name: 'centerSite' },
            { displayName: '来源人', name: 'resourceMan' },
            { displayName: '备注',
              name: 'remark',
              size: 260,
              isCode: true,
              value: function (row) {
                let remark = row.remark.replace(/,/g,"，");
                return remark
              }
            },
            { displayName: '操作人',
              name: 'editor',
              value: function (row) {
                if ("admin" == row.editor) {
                  return "";
                } else {
                  return row.editor;
                }
              }
            },
            { displayName: '操作时间', name: 'gmtModified', size: 130, formatter: 'yyyy-MM-dd hh:mm:ss'},
          ]
        },
        fineStatusOptions: [{id: -1, label: '全部'}, {id: 0, label: '未处罚'}, {id: 1, label: '已登记'}, {id: 2, label: '已处罚'}],
        fineTypeOptions: [],
        exportObj: {
          fileName: '网点罚款数据'
        },
        title: "",
        dialogVisible: false,
        batchUrl: "",
        paramOption:{},
        columns: [{
          displayName: '运单号',
          name: 'billCode'
        }],
        currentRow: [],
        optionsClone: {},
        requestUrl: fineDetailUrl,
        editDialogVisible: false,     //编辑业务员
        userId: '',
        interFineDialogVisible: false,   //内部判罚
        interFineParam: {},
        interFineOptions: [],
        chazhi:16
      }
    },
    methods: {
      startDateChange(value) {
        if(value){
          this.$refs.timePickerB.focus()
        }
      },
      onSearch(){
        this.queryContent.billCode = '';
        if(this.billCode){
          let bills = trim(this.billCode).split(/[,\r\n]/);
          if (bills.length > 1000) {
            this.msg('最多支持1000个运单号查询');
            return;
          }
          this.queryContent.billCode = bills.join(',');
        }else{
          let startDate = this.queryTime.startDate,
            endDate = this.queryTime.endDate;
          if(!startDate){
            this.msg('开始时间不能为空');
            return
          }else if(!endDate){
            this.msg('结束时间不能为空');
            return
          }
          if(moment(startDate).isAfter(endDate)){
            this.msg('开始时间不能大于结束时间');
            return
          }
          this.chazhi = this.queryContent.interUserId ? 31 : 16;
          let timeInterval = moment(endDate).subtract(this.chazhi, 'days');
          let isAfter = moment(timeInterval).isAfter(moment(startDate));
          if(isAfter) {
            this.msg('最多可查询'+this.chazhi+'天的数据,请重新选择时间');
            return
          }
        }
        this.pagination.currentPage = 1;
        this.queryContent.settlementStartDate = moment(this.queryTime.startDate).format('YYYY-MM-DD HH:mm:ss');
        this.queryContent.settlementEndDate = moment(this.queryTime.endDate).format('YYYY-MM-DD HH:mm:ss');
        this.queryContent.pageSize = this.pagination.pageSize;
        this.queryContent.currentPage = this.pagination.currentPage;
        this.query(this.queryContent)
      },
      query(param){
        if(param){
          this.optionsClone = cloneDeep(param);
        }
        fineDetailQuery(this.optionsClone).then(res => {
          if(res.code === 0 && res.data.list && res.data.list.length > 0){
            this.tableData = res.data.list;
            this.tableData.map(item => {
              if(item.remark) item.remark = item.remark.replace(/,/g,"，");
              if(item.interFineCode === 0){
                item.interFineCode = ''
              }
            });
            if(this.optionsClone.billCode){
              this.pagination.total = 0
            }else{
              this.pagination.total = res.data.totalRow;
            }
          }else if(res.code === 0){
            this.msg('暂无数据！');
            this.pagination.total = 0;
            this.tableData = [];
          }
        }).catch(res => {
          this.pagination.total = 0;
          this.tableData = [];
        })
      },
      exportNew(){
        if(this.optionsClone.billCode){
          if(this.tableData.length > 0){
            this.checklistData = this.tableData;
            this.checklistExport()
          }else{
            this.msg('没有数据无法导出!')
          }
        }else{
          this.searchExport()
        }
      },
      // 点击表格时隐藏下拉列表框
      onClickTable() {
        this.$refs.userSelect.autoBlur();
        this.$refs.statusSelect.visible = false;
        this.$refs.typeSelect.visible = false;
        this.$refs.timePickerA._data.pickerVisible = false;
        this.$refs.timePickerB._data.pickerVisible = false;
      },
      close() {
        this.dialogVisible = false;
        this.title = "";
        this.batchUrl = "";
        this.currentRow = [];
        this.query();
      },
      // 多选
      selectionSite(rows, row) {
        this.currentRow = this.tableData.filter(res => {
          return res.check === true;
        });
      },
      checkboxAllClick(val) {
        this.selectionSite([], val);
      },
      //点击批量按钮
      batchUpdate(e){
        if(this.currentRow.length === 0){
          this.msg('请至少选中一条要同步的数据！');
          return
        }
        this.title = e.target.innerText;
        this.batchUpdateAjax()
      },
      batchUpdateAjax(){
        // if(this.title === '同步账单揽收业务员'){
        //   this.batchUrl = '/fine/detail/synchronizingRecman';
        //   this.currentRow = this.currentRow.map(item => {
        //     return {
        //       billCode: item.billCode,
        //       id: item.id
        //     }
        //   });
        // }else if(this.title === '同步最新派件业务员'){
        //   this.batchUrl = '/fine/detail/synchronizing';
        //   this.currentRow = this.currentRow.map(item => {
        //     return {
        //       billCode: item.billCode,
        //       id: item.id
        //     }
        //   });
        this.columns = [{
          displayName: '运单号',
          name: 'billCode'
        }];
        if(this.title === '同步最新账单结算对象'){
          this.batchUrl = '/fine/detail/syncSettlementMan';
          this.currentRow = this.currentRow.map(item => {
            return {
              billCode: item.billCode,
              id: item.id
            }
          });
        }else if(this.title === '编辑业务员'){
          this.batchUrl = '/fine/detail/updateUser';
        }else if(this.title === '取消判罚'){
          this.batchUrl = '/fine/detail/judgmentByType';
          this.currentRow = this.currentRow.map(current => {
            return {
              id: current.id,
              cancelFine: 0,
              billCode: current.billCode
            }
          });
        }
        this.dialogVisible = true;
      },
      //打开批量编辑业务员页面
      editBatchUpdate(){
        if(!this.userId){
          this.msg('请选择关联业务员！');
          return
        }
        this.title = '编辑业务员';
        this.currentRow = this.currentRow.map(item => {
          return {
            billCode: item.billCode,
            userId: this.userId,
            id: item.id
          }
        });
        this.editDialogVisible = false;
        this.userId = '';
        this.batchUpdateAjax()
      },
      //选择业务员
      chooseUser(){
        if(this.currentRow.length === 0){
          this.msg('请至少选中一条要同步的数据！');
          return
        }
        this.editDialogVisible = true;
      },
      //内部判罚
      interFine(){
        if(this.currentRow.length === 0){
          this.msg('请选中一条要同步的数据！');
          return
        }else if(this.currentRow.length !== 1){
          this.columns = [
            {displayName: '运单号',name: 'billCode'},
            {displayName: '内部处罚人',name: 'interUserName'},
            {displayName: '罚款类型',name: 'fineType'},
            {displayName: '总部罚款金额',name: 'headFineFee'},
          ],
          this.currentRow = this.currentRow.map(item => {
            return {
              billCode: item.billCode,
              interUserName: item.interUserName,
              fineType: "罚款",
              headFineFee:item.headFineFee,
              id: item.id,
              cancelFine: 2,
              amount: item.headFineFee,
              fineTypeCode: 1064,
              interUserId:item.interUserId
            }
          });
          this.dialogVisible = true;
          this.batchUrl = '/fine/detail/judgmentByType';
        }else{
          this.interFineDialogVisible = true;
            setTimeout(() => {
              this.interFineParam = {...this.currentRow[0]};
          },0);
        }
        
      },
      //登记罚款
      registerInterFine(num){
        if(this.checkPunished()) return;
        let param = {
          id: this.interFineParam.id,
          cancelFine: num,
          amount: this.interFineParam.interFineFee,
          fineTypeCode: this.interFineParam.interFineCode,
          interUserId:this.interFineParam.interUserId
        };
        this.interFineParam.cancelFine = num;
        interFineAjax(param).then(res => {
          if(res.code === 0){
            this.$message({
              message: '操作成功',
              type: 'success'
            });
            this.query();
            this.currentRow = [];
          }else{
            this.msg(res.message)
          }
          this.interFineDialogVisible = false;
        })
      },
      checkPunished(){
        if(this.interFineParam.interStatus === 2){
          this.msg('已扣款的数据不可进行内部判罚!');
          return true
        }
        if(!this.interFineParam.scanMan){
          this.msg('关联业务员为空不能登记!');
          return true
        }
        if(!this.interFineParam.interUserId){
          this.msg('内部处罚人为空不能登记!');
          return true
        }
        if(!this.interFineParam.interFineCode){
          this.msg('请选择罚款类型!');
          return true
        }
        if(this.interFineParam.interFineFee !== 0 && this.interFineParam.interFineFee !== '0' && (this.interFineParam.interFineFee === '' || !this.interFineParam.interFineFee)){
          this.msg('内部处罚金额不能为空!');
          return true
        }
        if(!(/^\d+(\.\d+)?$/.test(this.interFineParam.interFineFee))){
          this.msg('请输入有效数字且最多能输入两位小数！');
          return true
        }
        if(this.checkNum(this.interFineParam.interFineFee)){
          this.msg('内部处罚金额小数点只能保留2位小数!');
          return true
        }
        if (Number(this.interFineParam.interFineFee) < 0 || Number(this.interFineParam.interFineFee) > 99999) {
          this.msg("内部处罚金额的范围是0~99999");
          return true
        }
      },
      checkNum(num){
        let status = false;
        if (num.toString().includes(".")) {
          let newArr = num.toString().split(".");
          let decL = newArr[1].length;
          if (decL > 2) {
            status = true;
          }
        }
        return status;
      }
    },
    computed: {
      contentHeight() {
        if(this.z_param.isActiveH){
          return this.$store.getters.clientHeight - 260;
        }else{
          if (this.z_param.isNode) {
            return this.$store.getters.clientHeight - 336;
          } else {
            return this.$store.getters.clientHeight - 276;
          }
        }
      }
    },
    watch: {
      switchOptions: {
        handler: function(newVal, oldval) {
          this.pagination.pageSize = 100;
          this.pagination.currentPage = 1;
          let params = {
            settlementStartDate: this.moment(newVal.businessDate).format(
              "YYYY-MM-DD 00:00:00"
            ),
            settlementEndDate: this.moment(newVal.businessDate).format(
              "YYYY-MM" + '-31 23:59:59'
            ),
            pageSize: 100,
            currentPage: 1,
            interUserId: newVal.interId,
            monthToDetail: true
          };
          this.query(params)
        },
        deep: true
      }
    }
  }
</script>

<style scoped lang="scss">
  .search-fine-box{
    display: flex;
    padding: 15px 10px 10px;
    .search-input-box{
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
      margin-left: 10px;
      li{
        margin-right: 7px;
        .short-input{
          width: 170px;
        }
        .select-input{
          width: 120px;
        }
        .label{
          color: #595959;
        }
      }
    }
  }
  .content-box{
    border-top: 10px solid #e9eef2;
    position: relative;
    padding-bottom: 20px;
    .btn-box{
      padding: 10px 10px 0;
      display: flex;
      justify-content: flex-end;
    }
    .page-box{
      text-align: right;
      position: absolute;
      bottom: -3px;
      right: 10px;
    }
  }
  .user-form{
    padding: 10px 0 10px 40px;
    display: flex;
    align-items: center;
    .form-title{
      width: 100px;
      text-align: right;
    }
