<template>
  <main>
    <el-tabs type="border-card" @tab-click="onTabClick" v-model="activeName">
      <el-tab-pane label="罚款明细" name="detail">
        <fine-detail :switchOptions="switchOptions"></fine-detail>
      </el-tab-pane>
      <el-tab-pane label="内部处罚汇总" name="summary">
        <inside-fine-collect @goto="goto"></inside-fine-collect>
      </el-tab-pane>
    </el-tabs>
  </main>
</template>

<script>
  import fineDetail from './tabs/fineDetail'
  import insideFineCollect from './tabs/insideFineCollect'
  export default {
    name: "branchFine",
    components: {
      fineDetail,
      insideFineCollect
    },
    data(){
      return {
        activeName: 'detail',
        switchOptions: {}
      }
    },
    methods: {
      onTabClick() {
        this.z_method.refreshSpread();
      },
      goto(name, args) {
        this.activeName = name;
        this.z_method.refreshSpread();
        this.switchOptions = args;
      }
    }
  }
</script>

